import React, {Component} from 'react';

class SearchBar extends React.Component{
    state={
        text:'',
    }
    handleChange=(e)=>{
        this.setState({
            [e.target.name]: e.target.value
        })
    }
    handleSubmit=(e)=>{
        e.preventDefault();
        this.props.data_from_SearchBar(this.state.text);
        this.setState({
            text:''
        })
    }
    render(){
        return(
            <div>
                <form onSubmit={this.handleSubmit} >
                    Search: <input onChange={this.handleChange} value={this.state.text} name='text' /><br />
                </form>
            </div>
        )
    }
}

export default SearchBar