import React from 'react';
import './App.css'
import SearchBar from './SearchBar.jsx';
import Images from './Images.jsx';
import axios from 'axios';

class App extends React.Component{
  state={
    images:[]
  }
  data_from_SearchBar= async (stuff)=>{
    this.setState({images:[]});
    axios.get('https://api.unsplash.com/search/photos',{
      params:{ query: stuff, per_page: 50 },
      headers:{
        Authorization: ":Client-ID 8109a13522b0d165f3c0153241612e33beaef848682bc10d093e0508b2927121"
      }
    }).then((data)=>{
      const x = this.state.images;
      data.data.results.forEach((a)=>{
        x.push(a.urls.regular);
      })
      this.setState({
        images:x
      })
    }).catch((error)=>{console.log(error)})
  }
  display=()=>{
    return this.state.images.map((a)=>{
      return <a href={a} key={Math.random()} target="_blank"><Images source={a} /></a>
    })
  }
  render(){
    return(
      <div className='App'>
        <h1>Image Search</h1>
        <p>using <a href='https://unsplash.com/'>Unsplash</a></p>
        <SearchBar data_from_SearchBar={this.data_from_SearchBar} />
        <div style={{display:'flex', flexWrap:'wrap', justifyContent:'center'}} >{this.display()}</div>
      </div>
    )
  }
}

export default App